var _this
import {
	setTokenStorage
} from '../../utils/tool.js'

export default {
	data() {
		return {
			infoRes: {},
			imgUrl: this.$URL.iconURL,
			title: 'login',
			providerList: [],
			hasProvider: false,
			positionTop: 0,
			psk: "",
			Ras: ""
		}
	},
	onLoad() {
		_this = this
		this.initPosition()
		this.initProvider()

	},
	methods: {
		//获取用户信息
		getUserInfo() {
			this.$http.getUserInfo().then(res => {
				console.log("用户信息", res)
				if (res.data.result.has_express == 1) {
					// 有权限登录
					_this.$http.getExpressUserInfo().then(res=>{
						console.log('物流信息',res)
						_this.$tool.setStorageSync("pickup",res.data.result)
						_this.$tool.setStorageSync("oldToken",_this.$tool.getTokenStorage())

						setTokenStorage(res.data.result.token)
						_this.$tool.setStorageSync('isLogin', true)
						uni.hideLoading();
						uni.reLaunch({
							url: '../index/index'
						})
					})

				} else {
					uni.hideLoading();
					this.$common.toast('暂无权限登录')
				}
			})
		},
		//微信主动唤起登录权限
		onGotUserInfo: function(e) {
			if (e.detail.rawData) {
				this.oauth('weixin')
				this.$tool.setStorageSync('userInfo', e.detail.rawData)
			} else {
				uni.showToast({
					title: '登录将体验更多功能',
					icon: 'none'
				})
			}
		},
		applogin(res) {
			_this.$http.appLogin(res).then(res => {
				// *app 登录没有其余多余的流程
				setTokenStorage(res.data.result.token)
				console.log(res)
				_this.getUserInfo()
			}).catch(err => {
				console.log(err)
			})
		},
		//此处登录之后重定向到首页
		goLogin(loginFrom) {
			// 获取token 
			this.$http.getToken(loginFrom).then(res => {
				if (res.data.result.status == 0) {
					setTokenStorage(res.data.result.token)
					_this.getUserInfo()
				} else if (res.data.result.status == 1) {
					//新用户注册				
					let thirdFrom = {
						oauthCode: loginFrom.code,
						encrypted_data: _this.infoRes.encrypted_data,
						iv: _this.infoRes.iv,
						signature: _this.infoRes.signature,
						unionid: res.data.result.unionid,
						...loginFrom
					}
					_this.$http.thirdLogin(thirdFrom).then(res => {
						setTokenStorage(res.data.result.token)
						_this.getUserInfo()
					})
				} else if (res.result.status == 2) {
					//老用户更新
					console.log(res.data.result.token)
					setTokenStorage(res.data.result.token)
					_this.getUserInfo()

				}
			}).catch(err => {
				console.log(err)
			})

		},

		initPosition() {
			this.positionTop = uni.getSystemInfoSync().windowHeight - 200;
		},
		//判断当前页面支持何种登录方式 且将渲染到页面上 当前选择微信和QQ
		initProvider() {
			const filters = ['weixin'];
			uni.getProvider({
				service: 'oauth',
				success: (res) => {
					if (res.provider && res.provider.length) {
						for (let i = 0; i < res.provider.length; i++) {
							if (~filters.indexOf(res.provider[i])) {
								this.providerList.push({
									value: res.provider[i],
									image: '../../static/img/' + res.provider[i] + '.png'
								});
							}
						}

						this.hasProvider = true;
					}
				},
				fail: (err) => {
					console.error('获取服务供应商失败：' + JSON.stringify(err));
				}
			});

		},

		// oauth 鉴权
		oauth(value) {
			uni.login({
				provider: value,
				success: (res) => {
					uni.getUserInfo({
						provider: value,
						success: (infoRes) => {
							_this.infoRes = {
								oauthCode: res.code,
								suffix: 'wx',
								iv: infoRes.iv,
								encrypted_data: infoRes.encryptedData,
								signature: infoRes.signature,
								...infoRes.userInfo
							}

							_this.infoRes.suffix = 'wxm'
							uni.showLoading({
								title: '登录中，请稍等',
								mask: true
							});
							_this.goLogin(_this.infoRes)
							console.log(_this.infoRes);










						}
					});
				},
				fail: (err) => {
					console.error('授权登录失败：' + JSON.stringify(err));
				}
			});
		},
	}
}
